import React, { useEffect, useState } from 'react';
import { Button, Menu, Select, Drawer, message } from 'antd';
import AMapLoader from '@amap/amap-jsapi-loader';
import NotGoTo from './component/NotGoTo';

function App() {
  const [provinceList, setProvinceList] = useState([]);
  const [cityList, setCityList] = useState([]);
  const [districtList, setDistrictList] = useState([]);

  const [selectedProvince, setSelectedProvince] = useState('');
  const [selectedCity, setSelectedCity] = useState('');
  const [selectedDistrict, setSelectedDistrict] = useState('');

  const [open, setOpen] = useState(false);

  const [subPage, setSubPage] = useState('');

  const showDrawer = () => {
    if(selectedCity === ''){
      message.info('');
      return;
    }
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    var map, district, polygons = [];

    window._AMapSecurityConfig = {
      securityJsCode: "2e803736ac0504da65adee814cc6d307",
    };

    AMapLoader.load({
      key: '16745912922e513b08ec609abc01ed10',
      version: '1.4.15',
      plugins: ['AMap.DistrictSearch', 'AMap.Polygon'],
    }).then((AMap) => {
      map = new AMap.Map('container', {
        center: [116.30946, 39.937629],
        zoom: 3
      });

      var opts = {
        subdistrict: 1,
        showbiz: false
      };
      district = new AMap.DistrictSearch(opts);
      district.search('', function (status, result) {
        if (status === 'complete') {
          getData(result.districtList[0], 'province');
        }
      });

      function getData(data) {
        var bounds = data.boundaries;
        if (bounds) {
          for (var i = 0, l = bounds.length; i < l; i++) {
            var polygon = new AMap.Polygon({
              map: map,
              strokeWeight: 1,
              // strokeColor: '#0091ea',
              // fillColor: '#80d8ff',
              strokeColor: 'pink',
              fillColor: 'pink',
              fillOpacity: 0.2,
              path: bounds[i]
            });
            polygons.push(polygon);
          }
          map.setFitView(); // 
        }

        var subList = data.districtList;
        console.log(subList);
        var level=subList[0].level;
        if (level === 'province') {
          setProvinceList(subList);
          setSelectedProvince('');
          setCityList([]);
          setSelectedCity('');
          setDistrictList([]);
          setSelectedDistrict('');
        } else if (level === 'city') {
          setCityList(subList);
          setSelectedCity('');
          setDistrictList([]);
          setSelectedDistrict('');
        } else if (level === 'district') {
          setDistrictList(subList);
          setSelectedDistrict('');
        }
      }

      window.search = function search(list, level, value) {
        // 
        for (var i = 0, l = polygons.length; i < l; i++) {
          polygons[i].setMap(null);
        }

        var option = list.find(item => item.name === value);
        if (!option) {
          return;
        }

        var adcode = option.adcode;
        district.setLevel(level); // 
        district.setExtensions('all');
        // 
        // adcode
        district.search(adcode, function (status, result) {
          if (status === 'complete') {
            getData(result.districtList[0]);
          }
        });
      }

      window.setCenter = function setCenter(obj) {
        map.setCenter(obj[obj.options.selectedIndex].center);
      }
    }).catch(e => {
      console.error(e);
    });

    return () => {
      // 
    };
  }, []);


  return (
    <div style={{position:'relative'}}>
      <img src="appIcon.png" alt="App Icon" style={{ right: '10px', top:"0px", position: 'absolute', width: '50px', height: '50px' }} />
      <Menu 
        mode="horizontal"
        style={{backgroundColor: 'pink'}}
      >
        <Menu.Item key="province">
          
          <Select 
            value={selectedProvince}
            style={{ width: '100px' }}
            onChange={(value) => {
              setSelectedProvince(value);
              window.search(provinceList, 'province', value);
            }}
            options={provinceList.map(item => ({ value: item.name, label: item.name }))}
          ></Select>
        </Menu.Item>
        <Menu.Item key="city">
          
          <Select 
            disabled={cityList.length === 0}
            value={selectedCity}
            style={{ width: '100px' }}
            onChange={(value) => {
              setSelectedCity(value);
              window.search(cityList, 'city', value);
            }}
            options={cityList.map(item => ({ value: item.name, label: item.name }))}
          ></Select>
        </Menu.Item>
        <Menu.Item key="district">
          
          <Select 
            disabled={districtList.length === 0}
            value={selectedDistrict}
            style={{ width: '100px' }}
            onChange={(value) => {
              setSelectedDistrict(value);
              window.search(districtList, 'district', value);
            }}
            options={districtList.map(item => ({ value: item.name, label: item.name }))}
          ></Select>
        </Menu.Item>
        <Menu.Item>
          <Button 
            type="default"
            style={{backgroundColor: 'pink', border: '1px solid black'}}
            onClick={showDrawer}
          >
            
          </Button>
        </Menu.Item>
      </Menu>
      <div id="container" style={{ width: '100%', height: '100vh' }}></div>
      <Drawer 
        title={selectedProvince+" "+selectedCity+" "+selectedDistrict}
        onClose={onClose} 
        open={open}
        width={'80%'}
        style={{backgroundColor: 'pink'}}
      >
        <Menu
          mode="horizontal"
          style={{backgroundColor: 'pink'}}
          onClick={(e) => {setSubPage(e.key)}}
        >
          <Menu.Item key="">
            <p></p>
          </Menu.Item>
          <Menu.Item key="">
            <p></p>
          </Menu.Item>
          <Menu.Item key="">
            <p></p>
          </Menu.Item>
        </Menu>
        {subPage === '' && <NotGoTo/>}
        {subPage === '' && <NotGoTo/>}
        {subPage === '' && <NotGoTo/>}
      </Drawer>
    </div>
  );
}

export default App;